import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { AssociationComponent } from './association.component';
import { AssociationSongComponent } from './association-song/association-song.component';
import {BoardComponent} from './board/board.component';
import {CommitteesComponent} from './committees/committees.component';
import {HonoraryMembersComponent} from './honorary-members/honorary-members.component';
import {VisionComponent} from './vision/vision.component';
import {OckhamRazorComponent} from './ockham-razor/ockham-razor.component';
import {StatuesRulesComponent} from './statues-rules/statues-rules.component';

const routes: Routes =
  [
    { path: '', component: AssociationComponent },
    { path: 'association_song', component: AssociationSongComponent },
    { path: 'board', component: BoardComponent },
    { path: 'committees', component: CommitteesComponent },
    { path: 'honorary_members', component: HonoraryMembersComponent },
    { path: 'vision', component: VisionComponent },
    { path: 'ockham_razor', component: OckhamRazorComponent },
    { path: 'statues_rules', component: StatuesRulesComponent }
  ];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class AssociationRoutingModule { }
